/** è¿”å›å½“å‰è§’è‰²å¡çš„æ€»ç»“ä¸–ç•Œä¹¦åç§° */
export function getWorldbookName(): string {
    const charName = getCurrentCharacterName();
    if (!charName) {
        throw new Error('[è‡ªåŠ¨æ€»ç»“] æœªæ‰“å¼€è§’è‰²å¡ï¼Œæ— æ³•è·å–ä¸–ç•Œä¹¦åç§°');
    }
    return `${charName}[è‡ªåŠ¨æ€»ç»“]`;
}

// ========== ä¸–ç•Œä¹¦ç”Ÿå‘½å‘¨æœŸ ==========

/** ç¡®ä¿å½“å‰è§’è‰²å¡çš„æ€»ç»“ä¸–ç•Œä¹¦å­˜åœ¨ï¼Œä¸å­˜åœ¨åˆ™åˆ›å»º */
export async function ensureWorldbook(): Promise<void> {
    const name = getWorldbookName();
    const existingNames = getWorldbookNames();
    if (!existingNames.includes(name)) {
        await createWorldbook(name, []);
        console.log(`[è‡ªåŠ¨æ€»ç»“] å·²åˆ›å»ºä¸–ç•Œä¹¦: ${name}`);
    }
}

/**
 * ä»å…¨å±€ä¸–ç•Œä¹¦åˆ—è¡¨ä¸­ç§»é™¤å…¶ä»–è§’è‰²å¡çš„æ€»ç»“ä¸–ç•Œä¹¦ï¼Œç¡®ä¿å½“å‰è§’è‰²å¡çš„åœ¨åˆ—è¡¨ä¸­
 */
export async function switchToCurrentCharWorldbook(): Promise<void> {
    const currentName = getWorldbookName();
    const globalBooks = getGlobalWorldbookNames();
    // è¿‡æ»¤æ‰å…¶ä»–è§’è‰²å¡çš„[è‡ªåŠ¨æ€»ç»“]ä¸–ç•Œä¹¦ï¼Œä¿ç•™å½“å‰çš„
    const filtered = globalBooks.filter(
        (name) => !name.endsWith('[è‡ªåŠ¨æ€»ç»“]') || name === currentName,
    );
    if (!filtered.includes(currentName)) {
        filtered.push(currentName);
    }
    await rebindGlobalWorldbooks(filtered);
}

// ========== å°æ€»ç»“æ¡ç›®æ“ä½œ ==========

/** è·å–æŒ‡å®šæ¥¼å±‚çš„å°æ€»ç»“æ¡ç›® */
export async function getMiniSummaryEntry(
    message_id: number,
): Promise<WorldbookEntry | undefined> {
    const worldbook = await getWorldbook(getWorldbookName());
    const entryName = `[å°æ€»ç»“-æ¥¼å±‚${message_id}]`;
    return worldbook.find((e) => e.name === entryName);
}

/** åˆ›å»ºæˆ–æ›¿æ¢æŒ‡å®šæ¥¼å±‚çš„å°æ€»ç»“æ¡ç›® */
export async function upsertMiniSummaryEntry(
    message_id: number,
    content: string,
): Promise<void> {
    const worldbookName = getWorldbookName();
    const entryName = `[å°æ€»ç»“-æ¥¼å±‚${message_id}]`;

    const worldbook = await getWorldbook(worldbookName);
    const existing = worldbook.find((e) => e.name === entryName);

    if (existing) {
        // å·²å­˜åœ¨åˆ™æ›¿æ¢ content
        await updateWorldbookWith(worldbookName, (wb) => {
            const entry = wb.find((e) => e.name === entryName);
            if (entry) {
                entry.content = content;
            }
            return wb;
        });
    } else {
        // ä¸å­˜åœ¨åˆ™æ–°å»º
        await createWorldbookEntries(worldbookName, [
            {
                ...MINI_SUMMARY_DEFAULTS,
                name: entryName,
                content,
                position: {
                    ...MINI_SUMMARY_DEFAULTS.position,
                    order: getMiniSummaryOrder(message_id),
                },
            },
        ]);
    }
}

/** åˆ›å»ºç©ºå ä½å°æ€»ç»“æ¡ç›®ï¼ˆåŒæ­¥é˜¶æ®µä½¿ç”¨ï¼‰ */
export async function createPlaceholderMiniSummary(message_id: number): Promise<void> {
    const worldbookName = getWorldbookName();
    const entryName = `[å°æ€»ç»“-æ¥¼å±‚${message_id}]`;

    const worldbook = await getWorldbook(worldbookName);
    const existing = worldbook.find((e) => e.name === entryName);

    if (!existing) {
        await createWorldbookEntries(worldbookName, [
            {
                ...MINI_SUMMARY_DEFAULTS,
                name: entryName,
                content: 'ï¼ˆæ€»ç»“ç”Ÿæˆä¸­...ï¼‰',
                position: {
                    ...MINI_SUMMARY_DEFAULTS.position,
                    order: getMiniSummaryOrder(message_id),
                },
            },
        ]);
    }
}

/** è·å–æ‰€æœ‰æœªå½’æ¡£ï¼ˆéå·è¦†ç›–èŒƒå›´å†…ï¼‰çš„å°æ€»ç»“æ¡ç›® */
export async function getUnarchivedMiniSummaries(): Promise<WorldbookEntry[]> {
    const worldbook = await getWorldbook(getWorldbookName());
    const metadata = getScriptData();

    // æ”¶é›†å·²å½’æ¡£çš„æ¥¼å±‚ ID
    const archivedIds = new Set<number>();
    for (const vol of metadata.volumes) {
        for (let id = vol.start_message_id; id <= vol.end_message_id; id++) {
            archivedIds.add(id);
        }
    }

    return worldbook.filter((entry) => {
        const match = entry.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/);
        if (!match) return false;
        const id = parseInt(match[1]);
        return !archivedIds.has(id);
    });
}

// ========== enabled åŒæ­¥ ==========

/** æ ¹æ®æ¥¼å±‚å¯è§æ€§å’Œå½’æ¡£çŠ¶æ€åŒæ­¥æ‰€æœ‰å°æ€»ç»“çš„ enabled */
export async function syncMiniSummaryEnabled(): Promise<void> {
    const settings = getSettings();
    const lastId = getLastMessageId();
    const visibleThreshold = lastId - settings.visible_floors + 1;
    const metadata = getScriptData();

    // æ”¶é›†å·²å½’æ¡£çš„æ¥¼å±‚ ID èŒƒå›´
    const archivedIds = new Set<number>();
    for (const vol of metadata.volumes) {
        for (let id = vol.start_message_id; id <= vol.end_message_id; id++) {
            archivedIds.add(id);
        }
    }

    await updateWorldbookWith(getWorldbookName(), (worldbook) => {
        for (const entry of worldbook) {
            const match = entry.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/);
            if (!match) continue;
            const id = parseInt(match[1]);

            if (id >= visibleThreshold) {
                // å¯è§æ¥¼å±‚ â†’ ä¸æ³¨å…¥
                entry.enabled = false;
            } else if (archivedIds.has(id)) {
                // å·²å½’æ¡£ â†’ ä¸æ³¨å…¥
                entry.enabled = false;
            } else {
                // å·²éšè—ä¸”æœªå½’æ¡£ â†’ æ³¨å…¥
                entry.enabled = true;
            }
        }
        return worldbook;
    });
}

// ========== å·æ¡ç›®æ“ä½œ ==========

/** åˆ›å»ºå·æ¡ç›®å¹¶å…³é—­å¯¹åº”èŒƒå›´å†…çš„å°æ€»ç»“ */
export async function createVolumeEntry(
    volume: number,
    start_id: number,
    end_id: number,
    content: string,
): Promise<void> {
    const worldbookName = getWorldbookName();
    const entryName = `[å·${volume}-æ¥¼å±‚${start_id}~æ¥¼å±‚${end_id}]`;

    await updateWorldbookWith(worldbookName, (worldbook) => {
        // 1. å…³é—­è¯¥èŒƒå›´å†…çš„å°æ€»ç»“æ¡ç›®
        for (const entry of worldbook) {
            const match = entry.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/);
            if (!match) continue;
            const id = parseInt(match[1]);
            if (id >= start_id && id <= end_id) {
                entry.enabled = false;
            }
        }

        // 2. æ·»åŠ å·æ¡ç›®
        worldbook.push({
            ...VOLUME_DEFAULTS,
            name: entryName,
            content,
            position: {
                type: 'at_depth',
                role: 'system',
                depth: 9999,
                order: getVolumeOrder(volume),
            },
        } as WorldbookEntry);

        return worldbook;
    });

    // 3. æ›´æ–°è„šæœ¬å˜é‡å…ƒæ•°æ®
    const data = getScriptData();
    data.current_volume = volume + 1;
    data.volumes.push({ volume, start_message_id: start_id, end_message_id: end_id });
    saveScriptData(data);
}

/** è·å–æ‰€æœ‰å·²æœ‰å·æ¡ç›® */
export async function getVolumes(): Promise<WorldbookEntry[]> {
    const worldbook = await getWorldbook(getWorldbookName());
    return worldbook.filter((entry) => /^\[å·\d+-æ¥¼å±‚\d+~æ¥¼å±‚\d+\]$/.test(entry.name));
}
