function reloadOnChatChange(): EventOnReturn {
    let chat_id = SillyTavern.getCurrentChatId();
    return eventOn(tavern_events.CHAT_CHANGED, (new_chat_id: string) => {
        if (chat_id !== new_chat_id) {
            chat_id = new_chat_id;
            window.location.reload();
        }
    });
}

